body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
ul{
  padding: 0;
}
li{
  list-style-type: none;
}
.container{
  max-width: 1200px;
  margin: 0 auto;
}
.languages{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.languages li{
  margin: 10px;
  font-weight: bold;
}
.popular-list{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;

}
.popular-item{
  margin: 20px;
  text-align: center;
}
.space-list-items{
  margin-bottom: 7px;
}
.avatar{
  width: 150px;
  border-radius: 50%;
}
a{
  text-decoration: none;
  color: #doo21d;
}
.popular-rank{
  font-size: 20px;
  margin: 20px;

}
.loading{
  position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 160px;
    overflow:hidden;
    font-family: 'Lato', sans-serif;
    font-size: 50px;
    line-height: 40px;
    color: tomato;
}
.active{
  font-weight: bold;
}
.Nav {
  display: -ms-flexbox;
  display: flex;
}
.Nav li{
  margin-right:15px;
}



